<template>
  <div class="goods-container" v-if="goodsDetail.details">
    <el-dialog
      :visible.sync="dialogVisible"
      width="900px"
      center
      :before-close="close"
    >
      <el-form label-width="130px">
        <el-form-item label="普通商品分类">
          <el-select v-model="value" clearable placeholder="请选择">
            <el-option
              v-for="(item, index) in goodsSort"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="商品名称">
          <div style="width: 400px">
            <el-input v-model="goodsDetail.goods_name" />
          </div>
        </el-form-item>
        <el-form-item label="封面">
          <el-upload
            ref="faceImg"
            style="display: inline"
            action
            :http-request="UploadFaceImg"
            list-type="picture-card"
            multiple
          >
            <i slot="default" class="el-icon-plus" />
          </el-upload>
          <transition name="el-fade-in-linear">
            <span v-show="isfaceImgShow">
              <img
                style="height: 150px; width: 150px"
                :src="goodsDetail.cover_img"
              >
            </span>
          </transition>
        </el-form-item>
        <el-form-item label="上传视频/图片">
          <el-upload
            style="display: inline"
            action
            :http-request="Upload"
            list-type="picture-card"
            ref="uploadFirst"
          >
            <i slot="default" class="el-icon-plus" />
          </el-upload>
          <transition name="el-fade-in-linear">
            <span v-show="isFirstImgShow">
              <img
                v-for="(item, index) of goodsDetail.first_img.split(',')"
                :key="index"
                style="height: 150px; width: 150px"
                :src="item"
              >
            </span>
          </transition>
        </el-form-item>
        <el-form-item label="规格">
          <div
            v-for="(item, index) in goodsDetail.specifications"
            :key="index"
            class="size"
          >
            <div class="title-container">
              <el-input style="width:250px" v-model="item.title" placeholder="请添加规格分类" />
              <span class="addRowItem" @click="addRowItem(index)">添加规格项</span>
              <span class="delete_btn" @click="deleteSizeItem(index)">
                删除
              </span>
            </div>
            <div class="innerInput" v-if="item.child.length > 0 ">
              <div v-for="(i, k) of item.child"
                   :key="k"
              >
                <el-input
                  style="width: 200px"
                  v-model="i.innerValue"
                  placeholder="请输入规格项,如颜色,尺寸"
                />
                <el-input
                  style="width: 200px"
                  v-model="i.cPrice"
                  placeholder="请输入价格"
                />
                <el-upload style="display:inline" :show-file-list="false" action="" :http-request="SizeItemUpload" :on-change="(file,fileList)=>{ hanldChange(file,fileList,index,k) }">
                  <el-button type="primary">
                    上传图片
                  </el-button>
                </el-upload>
              </div>
              
              <el-button
                icon="el-icon-delete"
                size="small"

                @click="deleteInnerItem(index)"
              />
              <el-button
                type="primary"
                icon="el-icon-plus"
                class="innerAdd"
                size="small"

                @click="addInnerItem(index)"
              />
            </div>
          </div>
          <el-button type="primary" @click="addSizeItem">
            添加规格
          </el-button>
        </el-form-item>
        <el-form-item label="库存">
          <div style="width: 300px">
            <el-input v-model="goodsDetail.inventory" />
          </div>
        </el-form-item>
        <el-form-item label="排序">
          <div style="width: 300px">
            <el-input v-model="goodsDetail.sort" />
          </div>
        </el-form-item>
        <el-form-item label="商品简介">
          <el-input type="textarea" :rows="5" v-model="goodsDetail.describe" />
        </el-form-item>
        <el-form-item label="商品详情">
          <el-upload
            style="display: inline"
            action
            :http-request="detailImgsUpload"
            list-type="picture-card"
            ref="uploadTwo"
          >
            <i slot="default" class="el-icon-plus" />
          </el-upload>
          <transition name="el-fade-in-linear">
            <span v-show="desImgShow">
              <img
                v-for="(item, index) of goodsDetail.details.split(',')"
                :key="index"
                style="height: 150px; width: 150px"
                :src="item"
              >
            </span>
          </transition>
        </el-form-item>
        <el-form-item label="原价">
          <el-input class="width" v-model="goodsDetail.original_price" />
        </el-form-item>
        <el-form-item label="现价">
          <el-input class="width" v-model="goodsDetail.market_price" />
        </el-form-item>
        <el-form-item label="混合支付">
          <el-switch v-model="isPay" />
          <el-select v-show="isPay" v-model="currentPay" class="select">
            <el-option
              v-for="(item, index) of payWay"
              :key="index"
              :label="item.title"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item v-if="isPay">
          <div class="input-container">
            <el-input
              placeholder="请输入积分数量"
              class="width"
              v-model="goodsDetail.integral_price"
            />
            <el-input
              placeholder="请输入优惠卷价格"
              class="width"
              v-model="goodsDetail.coupons_price"
            />
          </div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirm">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { client, setImgName } from "@/config/oss";
export default {
    data() {
        return {
            value: "",
            // 是否开启混合支付
            isPay: false,
            FirstImgShow: [],
            isfaceImgShow: true,
            isFirstImgShow: true,
            desImgShow: true,
            coverImg: "",
            desImgs: [],
            // 普通商品分类
            goodsSort: [],
            // 上传视频/图片
            sortImgUrl: "",
            // 规格列表项图片
            sizeItemImgUrl:"",
            // 详情图
            desImg: "",
            currentPay: " ",
            payWay: [
                {
                    title: "余额+积分+优惠券",
                    id: 1,
                },
               
                {
                    title: "积分",
                    id: 0,
                },
            ],
        };
    },
    props: {
        dialogVisible: {
            type: Boolean,
            defautl: false,
        },
        goodsDetail: {
            type: Object,
            default() {
                return {};
            },
        },
    },
    created() {
        this.getGoodsSort();
    },
    methods: {
    // 上传封面图
        async UploadFaceImg(file) {
            let url = await setImgName(client(), file,"faceImg");
            // eslint-disable-next-line camelcase
            this.coverImg = url;
            this.isfaceImgShow = false;
        },
        // 分类图片上传
        async Upload(file) {
            let url = await setImgName(client(), file,"sort");
            this.FirstImgShow.push(url);
            this.isFirstImgShow = false;
        },

        // 增加规格项列表数量
        addInnerItem(index) {
            if (this.goodsDetail.specifications[index].child.length < 10) {
                this.goodsDetail.specifications[index].child.push({
                    innerValue: "",
                    imageUrl:"",
                    cPrice:0
                });
            }
        },
        // 删除规格项列表数量
        deleteInnerItem (index){
            this.goodsDetail.specifications[index].child.pop();

        },
        addRowItem(index) {
            this.goodsDetail.specifications[index].child.push({
                innerValue: "",
                imageUrl:"",
                cPrice:0
            });
        },

        // 增加规格项
        addSizeItem() {
            if (!this.goodsDetail.specifications) {
                this.goodsDetail.specifications = [];
            }
            this.goodsDetail.specifications.push({
                title: "",
                child: [],
            });
        },
        // 删除规格项
        deleteSizeItem(index) {
            this.goodsDetail.specifications.splice(index, 1);
        },
        // 上传规格项目图
        async SizeItemUpload(file) {
            let url =  await setImgName(client(), file,"sizeImg");
            this.sizeItemImgUrl = url;
        },
        hanldChange(file,fileList,index,k){
            this.goodsDetail.specifications[index].child[k].imageUrl = this.sizeItemImgUrl;

        },
        // 详情图片上传
        async detailImgsUpload(file) {
            let url = await setImgName(client(), file,"detailImg");
            this.desImgs.push(url);
            this.desImgShow = false;
        },
        // 获取普通商品分类
        async getGoodsSort() {
            try {
                let res = await this.axios.get("/goods/config/nav/list", {
                    params: {
                        // 普通商品
                        type: 1,
                        pages: 1,
                        limit: 50,
                    },
                });
                this.goodsSort = res.data.list;
            } catch (error) {
                console.log(error);
            }
        },
        // 更新普通商品
        async confirm() {
            for(let i = 0;i < this.goodsDetail.specifications.length;i++){
                if(!this.goodsDetail.specifications[i].title){
                    this.$message.warning({
                        message:"请添加规格分类"
                    });
                    return;
                }
            }
            try {
                let res = await this.axios.post("/goods/update", {
                    id: this.goodsDetail.id,
                    pid: this.value || this.goodsDetail.pid,
                    lastPid: 0,
                    "pay_type": this.currentPay ? 0 : this.currentPay,
                    "cover_img": this.coverImg || this.goodsDetail.cover_img,
                    firstImg: this.FirstImgShow.join(",") || this.goodsDetail.first_img,
                    specifications: this.goodsDetail.specifications || [],
                    inventory: this.goodsDetail.inventory,
                    // 普通商品
                    type: 2,
                    describe: this.goodsDetail.describe,
                    goodsName: this.goodsDetail.goods_name,
                    sort: this.goodsDetail.sort,
                    details: this.desImgs.join(",") || this.goodsDetail.details,
                    oPrice: this.goodsDetail.original_price,
                    mPrice: this.goodsDetail.market_price,
                    iPrice: this.goodsDetail.integral_price,
                    cPrice: this.goodsDetail.coupons_price,
                });
                this.close();
                this.$emit("getData");
                this.$message({
                    message: res.msg,
                    type: "success",
                });
                console.log(res);
            } catch (error) {
                console.log(error);
            }
        },
        close() {
            this.value = "";
            this.desImgShow = true;
            this.isFirstImgShow = true;
            this.isfaceImgShow = true;
            this.$emit("setDialogVisible");
            this.$refs.faceImg.clearFiles();
            this.$refs.uploadFirst.clearFiles();
            this.$refs.uploadTwo.clearFiles();
        },
    },
    components: {},
};
</script>

<style lang="scss" scoped>
.goods-container {
  .size {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    .title-container {
      display: flex;
      justify-content: space-between;
      width: 500px;
      .delete_btn {
        width: 100px;
        height: 35px;
        line-height: 35px;
        text-align: center;
        border: 1px solid rgba(0,0,0,0.2);
        border-radius: 5px;
        cursor: pointer;
      }
      .addRowItem {
        width: 130px;
        height: 37px;
        line-height: 37px;
        text-align: center;
        background:#409EFF ;
        border-radius: 5px;
        color: white;
        cursor: pointer;
      }
    }
    .innerInput {
      margin-top: 5px;
    }
    .innerAdd {
      margin-left: 10px;
    }
  }
  .price {
    margin-left: 30px;
  }
  .width {
    width: 300px;
  }
  .select {
    margin-left: 15px;
  }
  .input-container {
    height: 90px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}
</style>
